.latest-articles {
  padding: 99px 0 53px;
  background-color: #f9fbff;
  .title {
    text-align: center;
    margin-bottom: 95px;
  }
  img {
    max-height: 222px;
  }
}

.latest-articles-list {
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
}

.latest-articles-media {
  position: relative;
  overflow: hidden;
  display: block;
  transition: all 0.3s ease;
}

.latest-articles-link {
  &:not(:hover) {
    color: #505b6a;
  }
  &:hover .latest-articles-media {
    opacity: .8;
  }
}

.latest-articles-title {
  padding: 25px 0 10px;
  margin: 0;
  text-transform: uppercase;
  display: inline-block;
  font-size: 16px;
  @include RobotoMedium;
}

.latest-articles-text {
  margin-bottom: 10px;
  line-height: 1.2;
}

.latest-articles-date {
  color: #b0aaaa;
  font-size: 15px;
  @include RobotoLight;
}

.latest-articles-all {
  padding: 40px 15px 0;
  text-align: right;
  clear: both;
  font-size: 16px;
  text-transform: uppercase;
  @include RobotoBold;
  a {
    &:not(:hover) {
      color: #6ba0b2;
    }
    .latest-articles-all_text {
      padding-bottom: 2px;
      border-bottom: 1px solid;
      display: inline-block;
    }
    &:hover .arrow-right {
      fill: #4f8ad9;
    }
  }
  .arrow-right {
    width: 20px;
    height: 12px;
    fill: #6ba0b2;
    vertical-align: middle;
    margin: -3px 0 0 5px;
    transition: all 0.3s ease;
  }
}
